.nav-pills .nav-link {
  font-size: 16px;
}

@media (max-width: 480px) {
  .nav-pills .nav-link {
    font-size: 12px;
  }
}

.nav-pills.with-background .nav-item {
  margin-right: 10px;
}

.nav-pills.with-background .nav-item:last-child {
  margin-right: 0;
}

.nav-pills.with-background .nav-link {
  background: white;
  padding: 10px 0;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.1);
}

.nav-pills.with-background .nav-link:hover {
  color: #5672f9;
}

.nav-pills.with-background .nav-link.active {
  background: #5672f9;
  position: relative;
  -webkit-transition: 0s;
  -moz-transition: 0s;
  -ms-transition: 0s;
  -o-transition: 0s;
  transition: 0s;
}

.nav-pills.with-background .nav-link.active:hover {
  color: #fff;
}

.nav-pills.with-background .nav-link.active:after {
  content: '';
  position: absolute;
  left: 42%;
  top: 100%;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #5672f9;
  clear: both;
  transition: all .2s ease-in 0s;
}

.nav-pills.transparent .nav-link {
  background: transparent;
  margin-bottom: 20px;
  color: #848484;
  font-size: 24px;
}

@media (max-width: 768px) {
  .nav-pills.transparent .nav-link {
    font-size: 17px;
  }
}

@media (max-width: 480px) {
  .nav-pills.transparent .nav-link {
    font-size: 12px;
  }
}

.nav-pills.transparent .nav-link.active {
  background: transparent;
  color: #666;
}

